import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from '../services/authentication.service';
import { Router } from '@angular/router';
import { NgxNavigationWithDataComponent } from 'ngx-navigation-with-data';
import { OsaService } from '../osa.service';
import { Product } from '../model/Product';


@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {

  DATA:Product[]
  user:string
  constructor(public nav:NgxNavigationWithDataComponent, private osaser:OsaService,private authService:AuthenticationService,private router:Router,public navCtrl:NgxNavigationWithDataComponent) { }

  ngOnInit(): void {

    this.osaser.getAllProducts().subscribe((data1)=>this.DATA=data1);
    if(sessionStorage.getItem('username')===null ||sessionStorage.getItem('username')===undefined){
      
      this.router.navigate(['login']);
    }
    else{
      
     this.user=this.navCtrl.get('name');
     this.user=sessionStorage.getItem('uname').toString();
    }
  }



  addtocart(productId:number){
    this.osaser.addtocart(productId).subscribe();
    this.router.navigateByUrl("/cart");
  }


  getproductinfo(productid:number){
    this.nav.navigate('/product',{productid:productid});
    }
}
