import { NgModule }      from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { AppComponent }  from './app.component';
import{FormsModule} from '@angular/forms';
import { MasterPageComponent } from './master-page/master-page.component';
import { AboutUsPageComponent } from './about-us-page/about-us-page.component';
import { OrderUsPageComponent } from './order-us-page/order-us-page.component';
import { AdminComponent } from './admin/admin.component';
import { LoginComponent } from './login/login.component';
import { ProductComponent } from './product/product.component';
import { TrackPageComponent } from './track-page/track-page.component';
import { RegisterComponent } from './register/register.component';
import { ContactUsPageComponent } from './contact-us-page/contact-us-page.component';
import { OsaService } from './osa.service';
import { Routes, RouterModule } from '@angular/router';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import { LogoutComponent } from './logout/logout.component';
import { AuthenticationService } from './services/authentication.service';
import { AuthGaurdService } from './services/auth-gaurd.service';
import { UserComponent } from './user/user.component';
import { NgxNavigationWithDataComponent } from 'ngx-navigation-with-data';
import { CartComponent } from './cart/cart.component';
import {DataTableModule} from 'angular-6-datatable';
import { CheckoutComponent } from './checkout/checkout.component';
import { AddressComponent } from './address/address.component';
import { OrderHistoryComponent } from './order-history/order-history.component';


const appRoutes:Routes=
[
    
    

  {path:'',redirectTo:'/master',pathMatch:'full'},
  {path:'master',component:MasterPageComponent},
  {path:'',redirectTo:'/track',pathMatch:'full'},
  {path:'track',component:TrackPageComponent},
  {path:'',redirectTo:'/register',pathMatch:'full'},
  {path:'register',component:RegisterComponent},

  {path:'',redirectTo:'/login',pathMatch:'full'},
  {path:'login',component:LoginComponent},

  {path:'',redirectTo:'/admin',pathMatch:'full'},
  {path:'admin',component:AdminComponent},

  {path:'',redirectTo:'/logout',pathMatch:'full'},
  { path: 'logout', component: LogoutComponent },

  {path:'',redirectTo:'/dashboard',pathMatch:'full',canActivate:[AuthGaurdService]},
  { path: 'dashboard', component: UserComponent },

  {path:'',redirectTo:'/cart',pathMatch:'full',},
  { path: 'cart', component: CartComponent },

  {path:'',redirectTo:'/checkout',pathMatch:'full',canActivate:[AuthGaurdService]},
  { path: 'checkout', component: CheckoutComponent },

  {path:'',redirectTo:'/order',pathMatch:'full',canActivate:[AuthGaurdService]},
  { path: 'order', component: OrderUsPageComponent },


  {path:'',redirectTo:'/addaddress',pathMatch:'full',canActivate:[AuthGaurdService]},
  { path: 'addaddress', component: AddressComponent},



  {path:'',redirectTo:'/history',pathMatch:'full',canActivate:[AuthGaurdService]},
  { path: 'history', component: OrderHistoryComponent},

  {path:'',redirectTo:'/product',pathMatch:'full',canActivate:[AuthGaurdService]},
  { path: 'product', component: ProductComponent}
  

  

]

@NgModule({
  imports:      [ BrowserModule,RouterModule.forRoot(appRoutes),FormsModule,HttpClientModule,DataTableModule],
  declarations: [ AppComponent ,UserComponent,MasterPageComponent, AboutUsPageComponent,
     OrderUsPageComponent, AdminComponent, LoginComponent, ProductComponent,
      TrackPageComponent, RegisterComponent, ContactUsPageComponent, 
      LogoutComponent, UserComponent, CartComponent, CheckoutComponent,
       AddressComponent, OrderHistoryComponent],
  providers: [OsaService,AuthenticationService,AuthGaurdService,NgxNavigationWithDataComponent],
  bootstrap:    [ AppComponent ]
})
export class AppModule { }