import { Component, OnInit } from '@angular/core';
import { OsaService } from '../osa.service';
import { Router } from '@angular/router';
import { OrderInvoice } from '../model/OrderInvoice';
import { Product } from '../model/Product';
import { FormGroup } from '@angular/forms';

@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.css']
})
export class AdminComponent implements OnInit {

  constructor(private osaser:OsaService,private router:Router) { }

    model:any={}
    show:boolean=false;
    orderInvoice:OrderInvoice;
    products:Array<Product>=new Array<Product>();
    adminform:FormGroup;
  ngOnInit(): void {
    if(!(localStorage.getItem('admin')=="yes"))
    {
      this.router.navigateByUrl("/login");
    }


    

  }

  alert(){
    
    this.osaser.getorderInfo(this.model.orderid).subscribe((data)=>{this.orderInvoice=data});
    console.log(this.orderInvoice)
    let array:string[];
    array=this.orderInvoice.idsOfProductpurchased;
    array.forEach(element => {
      let productid:number=Number.parseInt(element);
      
      this.osaser.getproductbyid(productid).subscribe((data)=>{this.products.push(data)});
    });
    console.log(this.products)
    console.log(this.orderInvoice)
    this.show=true
  }

}
