import { Component, OnInit } from '@angular/core';
import { OsaService } from '../osa.service';
import { NgxNavigationWithDataComponent } from 'ngx-navigation-with-data';
import { Product } from '../model/Product';
import { Router } from '@angular/router';

@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.css']
})
export class ProductComponent implements OnInit {
productid:number;
product:Product
  constructor(private route:Router ,private osaser:OsaService,public nav:NgxNavigationWithDataComponent) { }

  ngOnInit(): void {


this.productid=Number.parseInt(this.nav.get('productid'));
    this.osaser.getproductbyid(this.productid).subscribe((pro)=>{this.product=pro});
  }


  addtocart(productId:number){
    this.osaser.addtocart(productId).subscribe();
    this.route.navigateByUrl("/cart");
  }

}
