import { Component, OnInit } from '@angular/core';
import { OsaService } from '../osa.service';
import { Router } from '@angular/router';
import { Item } from '../model/Item';
import { NgxNavigationWithDataComponent } from 'ngx-navigation-with-data';
import { Payment } from '../model/Payment';

@Component({
  selector: 'app-address',
  templateUrl: './address.component.html',
  styleUrls: ['./address.component.css']
})
export class AddressComponent implements OnInit {

  constructor(public nav:NgxNavigationWithDataComponent,private osaser:OsaService,private router:Router,public navCtrl:NgxNavigationWithDataComponent) { }
cartId:number;
noOfItems:number;
total:number;
model:any={};
payment:Payment
orderinvoice:any;
  ngOnInit(): void {

    if(localStorage.getItem('customerId')==null|| localStorage.getItem('customerId')==undefined ||localStorage.getItem('cartId')==undefined){
      this.router.navigateByUrl("/login");
    }
    
    this.noOfItems=this.navCtrl.get('itemlength');
    this.total=this.navCtrl.get('carttotal');
    console.log("itemlengths"+this.noOfItems+" ***** "+this.total);
  this.cartId=Number.parseInt(localStorage.getItem('cartId'));
        
    
  }



  orderplced(){
    alert("Order Placed Successfully !!")
    console.log("hello")
    let input={
      "cart": {
        "cartId": this.cartId,
        "totalPrice": this.total
      },
      "customer": {
        "cart": {
          "cartId": this.cartId,
          "totalPrice": this.total
        },
        "customerEmailId": this.model.email,
        "customerId": localStorage.getItem('customerId'),
        "customerName": this.model.fname
      },
      "orderId": 0,
      "orderStatus": "In process",
      "orderTimeStamp": "string",
      "payment": {
        "paymentId": 0,
        "paymentInvoice": {
          "cardNo": this.model.ccnum,
          "cvvno": this.model.cvv,
          "expiryDate": this.model.expmonth+"/"+this.model.expyear,
          "ordertotal": this.total
        },
        "paymentMode": "cardpayment",
        "paymentStatus": "success"
      },
      "shippingAddress": {
        "address": this.model.address,
        "addressType": "shippingaddress",
        "city": this.model.city,
        "country": "India",
        "state": this.model.state,
        "zipcode": this.model.zip
      }
    }
      let id:number;
      this.osaser.placeOrder(input).subscribe((data)=>{this.orderinvoice=data});
      alert("OrderId "+this.orderinvoice.orderId);

      this.nav.navigate('/history',{orderid:this.orderinvoice.orderId,addr:this.model.address+","+this.model.city+","+this.model.zip});
    }



  

}


