import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { UserLogin } from '../model/UserLogin';

@Injectable({
  providedIn: 'root'
})
export class AuthenticationService {

  

  constructor(private http:HttpClient) { }


userlogin(email:string,pwd:string):any{
  console.log(email)
  
  return this.http.get<UserLogin>('http://localhost:8092/user/login/'+email+'/'+pwd)
}


  authenticate(username, password,logininstance:UserLogin) {
  
    

    if (logininstance!=undefined||(username === logininstance.useremail 
      || username === logininstance.username) && password === logininstance.userPwd) {
      sessionStorage.setItem('username', username)
      localStorage.setItem('cartId',logininstance.cartId.toString())
      return true;
    } else {
      return false;
    }
  }
  
  

  isUserLoggedIn() {
    let user = sessionStorage.getItem('username')
    console.log(sessionStorage.getItem('username'))
    console.log(!(user === null))
    return !(user === null)
  }

  logOut() {
    sessionStorage.removeItem('username')
  }
}