import { Component, OnInit } from '@angular/core';
import { OsaService } from '../osa.service';
import { AuthenticationService } from '../services/authentication.service';
import { Router } from '@angular/router';
import { OrderInvoice } from '../model/OrderInvoice';
import { OrderedItems } from '../model/OrderedItems';
import { NgxNavigationWithDataComponent } from 'ngx-navigation-with-data';
import { Order } from '../model/Order';

@Component({
  selector: 'app-order-history',
  templateUrl: './order-history.component.html',
  styleUrls: ['./order-history.component.css']
})
export class OrderHistoryComponent implements OnInit {

  constructor(private osaser:OsaService,
    private authService:AuthenticationService,private router:Router,
    public nav:NgxNavigationWithDataComponent) { }

  oitems:OrderedItems[];
  OrderInv:OrderInvoice;
  orderid:number;
  ordr:Order;
  addr:string;
  ngOnInit(): void {
    if(!(this.nav.get('orderid')==null)){
    this.orderid=Number.parseInt(this.nav.get('orderid'));
    this.addr=this.nav.get('addr');
    }
   // this.orderid=160;
    this.osaser.getorderInfo(this.orderid).subscribe((dta)=>{this.OrderInv=dta});
    this.osaser.getorderedItems(this.orderid).subscribe((data)=>{this.oitems=data});
    this.osaser.getinstance(this.orderid).subscribe((o)=>{this.ordr=o});
    console.log(this.ordr);
    
  }

  ordershipped(){
    if(this.ordr.orderStatus=="shipped" || this.ordr.orderStatus=="delivered"){
      return true;
    }
    return false;
  }

  orderdelivered(){
    if(this.ordr.orderStatus=="delivered"){
      return true;
    }
    return false;
  }

  ordersuccess(){
    if(this.ordr.orderStatus=="success"){
      return true;
    }
    return false;
  }

  






}
