import { Component, OnInit } from '@angular/core';

import { Router } from '@angular/router';
import { OsaService } from '../osa.service';
import { AuthenticationService } from '../services/authentication.service';
import { NgxNavigationWithDataComponent } from 'ngx-navigation-with-data';
import { UserLogin } from '../model/UserLogin';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  
  ngOnInit(): void {


    if(this.nav.get('uname')=="guest123@gmail.com"){
      this.model.username="guest123@gmail.com";
      this.model.password="Guest123@";
      
this.checkLogin();
    }


  }

logininstance:UserLogin;
  model:any={};

  username:string = this.model.username
  password:string = this.model.password
  invalidLogin = false

  constructor(private router: Router,
    private loginservice: AuthenticationService,public navCtrl:NgxNavigationWithDataComponent,public nav:NgxNavigationWithDataComponent ) { }


    checkLogin() {

      
     if(this.model.username=="admin.osa.123@osa.com" && this.model.password=="Admin123@")
      {
        this.invalidLogin=false
        this.router.navigateByUrl("/admin");
        localStorage.setItem('admin',"yes");
      }


     
      
      this.loginservice.userlogin(this.model.username, this.model.password).subscribe((data)=>this.logininstance=data);

      if (this.loginservice.authenticate(this.model.username, this.model.password,this.logininstance)
      ) {
        
        
        localStorage.setItem('customerId',this.logininstance.customerId.toString());
        localStorage.setItem('uname',this.logininstance.useremail);
        console.log(localStorage.getItem('customerId'));
        console.log()
        this.navCtrl.navigate('/dashboard', {name:this.logininstance.username});
        this.invalidLogin = false
      } 
      
      else
        this.invalidLogin = true
    }


  newuser() {
    this.router.navigateByUrl("/register");
  }


}
