import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Ordr } from '../model/Ordr';
import { OsaService } from '../osa.service';
import { OrderedItems } from '../model/OrderedItems';

@Component({
  selector: 'app-track-page',
  templateUrl: './track-page.component.html',
  styleUrls: ['./track-page.component.css']
})
export class TrackPageComponent implements OnInit {

  constructor(private router:Router,private osaser:OsaService) { }
model:any={}
ordr:Ordr;
addr:string;
oitems:OrderedItems

  ngOnInit(): void {
  
  }




alert(){
this.osaser.getorder(this.model.orderid).subscribe((order)=>{this.ordr=order});
this.osaser.getorderedItems(this.model.orderid).subscribe((data)=>{this.oitems=data});
this.addr=this.ordr.shippingAddress.address+" , "+this.ordr.shippingAddress.state+" , "+this.ordr.shippingAddress.zipcode


this.ordersuccess();
this.ordershipped();
this.orderdelivered();
console.log(this.ordr);

}



ordershipped(){
  if(this.ordr.orderStatus=="shipped" || this.ordr.orderStatus=="delivered"){
    return true;
  }
  return false;
}

orderdelivered(){
  if(this.ordr.orderStatus=="delivered"){
    return true;
  }
  return false;
}

ordersuccess(){
  if(this.ordr.orderStatus=="success"||this.ordr.orderStatus=="shipped" || this.ordr.orderStatus=="delivered"){
  
    return true;
  }
  return false;
}

cancelorder(){
  alert("Order Cancelled !!");
  this.router.navigateByUrl("/dashboard")
}

  

}
