import { Component, OnInit } from '@angular/core';
import { NgxNavigationWithDataComponent } from 'ngx-navigation-with-data';
import { Router } from '@angular/router';
import { AuthenticationService } from '../services/authentication.service';
import { OsaService } from '../osa.service';
import{Payment} from '../model/Payment'
@Component({
  selector: 'app-checkout',
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.css']
})
export class CheckoutComponent implements OnInit {

  constructor(private osaser:OsaService,private authService:AuthenticationService,private router:Router,public navCtrl:NgxNavigationWithDataComponent) { }
grandtotal:number;
cardno:string;
expno:string;
cvvno:string;
cartId:number;
pmt:Payment;

;
  ngOnInit(): void {
    if(sessionStorage.getItem('username')===null ||sessionStorage.getItem('username')===undefined){
      
      this.router.navigate(['login']);
    }
    localStorage.getItem('customerId');
    this.cartId=Number.parseInt(localStorage.getItem('cartId').toString());
    this.osaser.getcarttotal(this.cartId).subscribe((data)=>{this.grandtotal=data});


  
  }


  makePayment():any{

this.pmt={
  "paymentId": 0,
  "paymentInvoice": {
    "cardNo": this.cardno,
    "cvvno": this.cvvno,
    "expiryDate": this.expno,
    "ordertotal": this.grandtotal
  },
  "paymentMode": "cardpayment",
  "paymentStatus": "success"
}
console.log(this.pmt);
return this.osaser.makepayment(this.pmt).subscribe();



}

  }


