import { Component, OnInit } from '@angular/core';
import { Item } from '../model/Item';
import { Order } from '../model/Order';
import { OsaService } from '../osa.service';
import { Router } from '@angular/router';
import { NgxNavigationWithDataComponent } from 'ngx-navigation-with-data';

@Component({
  selector: 'app-order-us-page',
  templateUrl: './order-us-page.component.html',
  styleUrls: ['./order-us-page.component.css']
})
export class OrderUsPageComponent implements OnInit {
  items:Item[]
 order:Order;
 cartId:number;
total:number;
  constructor(private osaser:OsaService,private Router:Router,public navCtrl:NgxNavigationWithDataComponent) { }

  ngOnInit(): void {
    if(sessionStorage.getItem('username')===null ||sessionStorage.getItem('username')===undefined){
      
      this.Router.navigate(['login']);
    }
    this.cartId=Number.parseInt(localStorage.getItem('cartId').toString());
    this.osaser.getItemsinCart(this.cartId).subscribe((data)=>{this.items=data});
    this.osaser.getcarttotal(this.cartId).subscribe((data)=>{this.total=data});

  }
  back(){
    this.Router.navigateByUrl("/cart")
  }
  
  removeitemfromcart(itemId:number):any{
    this.osaser.removeitemfromcart(itemId).subscribe();
    alert("item removed from cart");
    
  }
  chkout(){
    
    this.navCtrl.navigate('/addaddress',{itemlength:this.items.length,carttotal:this.total});
  }
  continueshopping(){
    this.Router.navigateByUrl("/dashboard");
  }


}
