import { Component, OnInit } from '@angular/core';
import { Item } from '../model/Item';
import { OsaService } from '../osa.service';
import { Router } from '@angular/router';
import { AuthenticationService } from '../services/authentication.service';
import { NgxNavigationWithDataComponent } from 'ngx-navigation-with-data';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.css']
})
export class CartComponent implements OnInit {

  constructor(public nav:NgxNavigationWithDataComponent,  private osaser:OsaService,private router:Router,public loginService:AuthenticationService) { }
  item:Item;
  id:number;
  custid:number;
  cartid:number;
  carttotal:number;
  allitemsincart:Item[];
  ngOnInit(): void {
    console.log(localStorage.getItem('customerId'));
    if((localStorage.getItem('customerId')==undefined) || (localStorage.getItem('customerId')==null)){
      this.cartid=243;
    }
    else{
      this.custid=Number.parseInt(localStorage.getItem('customerId').toString());
      this.cartid=Number.parseInt(localStorage.getItem('cartId').toString());
     
              
    }
    console.log("cart to be loaded"+this.cartid)
    this.osaser.getcarttotal(this.cartid).subscribe((data)=>{this.carttotal=data});
    this.osaser.getItemsinCart(this.cartid).subscribe((data)=>{this.allitemsincart=data});

  }


  removeitemfromcart(itemId:number):any{
    this.osaser.removeitemfromcart(itemId).subscribe();
    alert("item removed from cart");
    setTimeout("location.reload(true);",10);
    this.router.navigate(['cart']);
  }

  removeallitemsfromcart(cartId:number):any{
    this.osaser.removeallitemsfromcart(this.cartid).subscribe();
    setTimeout("location.reload(true);",10);
    this.router.navigate(['cart']);
  }

  chkout(){
    this.router.navigateByUrl("/order");
  }

  continueshopping(){
    this.router.navigateByUrl("/dashboard")
  }

getproductinfo(productid:number){
this.nav.navigate('/product',{productid:productid});
}

}
